
.Users{
    width: 550px;
    background-color: white;
    border-radius: 15px;
    padding: 5px 20px;
    margin-bottom: 20px;
}



.User{
    background-color: white;
    border-bottom: #b0b0b1 1px solid;
    padding: 12px 0 12px;
    display: flex;
}

.userPhoto img{
    margin-right: 15px;
    width: 80px;
    height: 80px;
    border-radius: 55px;
}

.userName {
    
    font-weight: 700;
    margin-bottom: 9px;
}

.userName a {
    color: #2a5885;
    text-decoration: none;
}

.userInfo {
    width: 400px;
    display: flex;
    flex-direction: column;
    padding: 5px 10px 0 0;
}




.addFriend img {  
    width: 24px;
    height: 24px;
}

.addFriend{
    border-radius: 30px;
    width: 24px;
    height: 24px;
    padding: 10px;
    cursor: pointer;
}

.addFriend:hover {
    background-color: #eef6ff;
}