.item{
    width: 20px;
    text-align: center;
    margin-right: 5px;
    border-radius: 5px;
    background-color: aliceblue;
    box-shadow: 0px 0.5px 1.5px rgba(63, 63, 63, 0.3);
    color: rgb(139, 126, 126);
    font-size: 14px;
    cursor: pointer;
}

.itemDisabled{
    width: 20px;
    text-align: center;
    margin-right: 5px;
    border-radius: 5px;
    background-color: rgba(134, 133, 133, 0.3);
    box-shadow: 0px 0.5px 1.5px rgba(63, 63, 63, 0.3);
    color: rgb(139, 126, 126);
    font-size: 14px;
}

.paginator {
    margin-top: 10px;
    display: flex;
}

.selectedPage{
    font-weight: bold;
}

.pagination{
}

.item:hover {
    background-color: rgb(240, 240, 240);
    transition: ease 0.5ms;
}